import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'

import first from './components/first.vue'
import course from './components/course.vue'
import project from './components/projectRoute.js'

Vue.config.productionTip = false

Vue.use(VueRouter)

const routes = [
{
  path: '*',
  component: first,
},
{
  path: '/',
  component: first,
  alias: '/first',
},
{
  path: '/course',
  component: course
},
...project,

]

const router = new VueRouter({
  mode: 'hash',
  base: "/portfolio/",
  routes,
  scrollBehavior () {
    return { x: 0, y: 0 };
  }
})

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
